const PRODUCTS_BASE_URL = "https://goldsilkaws.metashopping.kr/products"; //  URL
const CART_BASE_URL = "https://goldsilkaws.metashopping.kr/cart"; //  URL

//   
export async function fetchProducts() {
  try {
    const response = await fetch(PRODUCTS_BASE_URL, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "ngrok-skip-browser-warning": "69420",
        Authorization: `Bearer ${localStorage.getItem("access_token")}`, //  
      },
    });

    if (!response.ok) {
      throw new Error(`Failed to fetch products: ${response.status}`);
    }

    return await response.json();
  } catch (error) {
    console.error("Error fetching products:", error.message);
    return [];
  }
}

//    (ID )
export async function fetchProductById(product_id) {
  try {
    const response = await fetch(`${PRODUCTS_BASE_URL}/${product_id}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "ngrok-skip-browser-warning": "69420",
        Authorization: `Bearer ${localStorage.getItem("access_token")}`, //  
      },
    });

    if (!response.ok) {
      throw new Error(
        `Failed to fetch product with ID ${product_id}: ${response.status}`
      );
    }

    return await response.json();
  } catch (error) {
    console.error(
      `Error fetching product with ID ${product_id}:`,
      error.message
    );
    return null;
  }
}

//   
export async function createProduct(productData) {
  try {
    const response = await fetch(PRODUCTS_BASE_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "ngrok-skip-browser-warning": "69420",
        Authorization: `Bearer ${localStorage.getItem("access_token")}`, //  
      },
      body: JSON.stringify(productData),
    });

    if (!response.ok) {
      throw new Error(`Failed to create product: ${response.status}`);
    }

    const createdProduct = await response.json();
    return createdProduct;
  } catch (error) {
    console.error("Error creating product:", error.message);
    return null;
  }
}

